@font-face {
  font-family: "TT Lakes";
  src: url(./fonts/TTLakes-Regular.eot);
  src: url(./fonts/TTLakes-Regular.eot?#iefix) format("embedded-opentype"), url(./fonts/TTLakes-Regular.woff) format("woff"), url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "TT Lakes";
  src: url(./fonts/TTLakes-Medium.eot);
  src: url(./fonts/TTLakes-Medium.eot?#iefix) format("embedded-opentype"), url(./fonts/TTLakes-Medium.woff) format("woff"), url(./fonts/TTLakes-Medium.ttf) format("truetype");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: "TT Lakes";
  src: url(./fonts/TTLakes-Bold.eot);
  src: url(./fonts/TTLakes-Bold.eot?#iefix) format("embedded-opentype"), url(./fonts/TTLakes-Bold.woff) format("woff"), url(./fonts/TTLakes-Bold.ttf) format("truetype");
  font-weight: bold;
  font-style: normal; }

html,
body {
  font-family: "TT Lakes";
  font-weight: 500;
  background-color: #f8f8f8; }

* {
  box-sizing: border-box; }

.brands__title {
  padding: 16px; }

.brands__expand {
  margin-left: 24px; }

.title {
  width: 100%;
  max-height: 80px;
  background: linear-gradient(90deg, #f8f8f8 1.39%, #ffffff 100%); }
  .title h1 {
    margin: 0;
    font-family: "TT Lakes";
    font-weight: normal;
    text-transform: uppercase;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #7e7e82;
    font-size: 16px; }

.swiper-container {
  padding-left: 16px;
  padding-bottom: 52px; }

.swiper-wrapper {
  margin-top: 16px; }

.swiper-slide {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 16px;
  max-width: 240px;
  height: 72px;
  cursor: pointer;
  border-radius: 6px;
  border: 1px solid #eaeaea;
  background-color: #ffffff; }
  .swiper-slide:hover .swiper__button {
    transform: scale(1.05) rotate(90deg); }
  .swiper-slide-expanded {
    display: flex; }

.swiper__button {
  width: 40px;
  height: 40px;
  border: 2px solid #ff3e79;
  border-radius: 20px;
  background: url(/img/brands/arrow.svg) center no-repeat;
  transition: 0.5s;
  transition-property: transform; }

.swiper-pagination {
  bottom: 24px !important; }

.expand {
  display: none; }
  .expand__arrows {
    transition: transform 0.25s; }
    .expand__arrows-rotated {
      transform: rotate(-180deg); }

@media screen and (min-width: 768px) {
  .brands__title {
    padding: 16px; }
  .brands .swiper-container {
    overflow: visible;
    padding: 0;
    padding-bottom: 24px; }
  .brands .swiper-wrapper {
    box-sizing: border-box;
    display: grid;
    justify-items: center;
    grid-template-columns: repeat(3, 1fr);
    gap: 24px;
    margin-top: 24px;
    padding: 0 24px; }
  .brands .swiper-pagination {
    display: none; }
  .brands .expand {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;
    max-width: 133px;
    height: 24px; } }

@media screen and (min-width: 768px) and (max-width: 1030px) {
  .swiper-slide:nth-last-of-type(1),
  .swiper-slide:nth-last-of-type(2) {
    display: none; } }

@media screen and (min-width: 1030px) {
  .brands .swiper-wrapper {
    grid-template-columns: repeat(4, 1fr); } }
